<template>
  <div>
    <div class="py-5 text-center" style="width: 100%; min-height: 60rem;">
      <div class="container px-4">
        <div id="nodesMap" style="height: 38rem; background-color: rgb(71, 71, 71);" class="img-fluid border rounded-3 shadow-lg mb-4">
          <h1> Przepraszam, mapa nie działa </h1>
        </div>
      </div>
      <h1 class="display-5 fw-bold"> Mapa punktów sprzedaży naszych piw</h1>
    
    </div>
  </div>
</template>
<script>
  import "leaflet/dist/leaflet.css";
  import axios from 'axios';
import L from "leaflet";
import "@/utilities.js"
import { middle_of_stores } from "@/utilities";
export default {
  name: 'App',
  methods: {
    setupLeafletMap: function (stores) {
      const mapDiv = L.map("nodesMap").setView(middle_of_stores(stores), 14);
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '<a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(mapDiv)
      stores.forEach((store) => {
        L.marker([store.latitude, store.longitude], {icon: L.icon({
            iconUrl: "marker_green.png",
            iconSize: [30, 30],
            iconAnchor: [15, 30],
            popupAnchor: [0, -30]
          })}).addTo(mapDiv)
          .bindPopup(`<p><b>Nazwa:</b><br>${store.name}</p>
                      <p><b>Adres:</b><br>${store.address}</p>
                      <a href="${store.url}">Strona lokalu</a>`)
      })
    },
  },
  async mounted() {
    axios.get("https://raw.githubusercontent.com/witoldkesek/swietoslaw-page/master/users-data.json")
    .then( response => {
      this.setupLeafletMap(response.data);
    })
  }
}
</script>